import Vue from 'vue'
import VueRouter from 'vue-router'
import store from 'store/index'

Vue.use(VueRouter)

// 登录
const LoginIndex = () => import('views/login/LoginIndex');
const Login = () => import('views/login/Login');
const Register = () => import('views/login/Register');

// 用户中心
const UserIndex = () => import('views/user/UserIndex');
const User = () => import('views/user/User');
const Search = () => import('views/user/Search');

const routes = [
  {
    path: '/',
    redirect: '/login/login'
  },
  {
    path: '/login',
    component: LoginIndex,
    meta: {
      islogin: false
    },
    children:[
      {
        path:'register',
        name: 'register',
        component:Register,
        meta: {
          islogin: false
        },
      },
      {
        path:'login',
        name: 'login',
        component:Login,
        meta: {
          islogin: false
        },
      }
    ],
  },
  {
    path: '/user',
    name: 'user',
    component: UserIndex,
    meta: {
      islogin: false
    },
    children:[
      {
        path:'search',
        name: 'search',
        component:Search,
        meta: {
          islogin: false
        },
      },
      {
        path:'user',
        name: 'user',
        component:User,
        meta: {
          islogin: false
        },
      }
    ],
  },
];
// 页面刷新时，重新赋值token
if (window.localStorage.getItem('Us')) {
  store.commit('login', window.localStorage.getItem('Us'))
}
const router = new VueRouter({
  // mode: 'history',
  // base: process.env.BASE_URL,
  routes
})

router.beforeEach((to, from, next) => {
  if (to.matched.some(r => r.meta.islogin)) {
    if (store.state.Us) {
      next();
    }
    else {
      next({
        path: '/login/login',
        // query: {redirect: to.fullPath}
      })
    }
  }
  else {
    next();
  }
})

export default router
